import React from 'react';
import ScrollToTop from 'react-scroll-up';
import TextLoop from "react-text-loop";
import FooterTwo from "./component/footer/FooterTwo";
import {FiChevronUp} from "react-icons/fi";
import Header from "./component/header/HeaderFour";
import TabThree from "./elements/tab/TabThree";
import Helmet from "./component/common/Helmet";
import Sketch from "react-p5";

class App extends React.Component{  

   // added by me to put in new 
   grid;
   next
   cols;
   rows;
   resolution = 14;
   count = 0
   totcount = 0
   //
   

   setup = (p5, canvasParentRef) => {
       let canvas = p5.createCanvas(p5.windowWidth, p5.windowHeight).parent(canvasParentRef);
       this.cols = Math.floor(p5.windowWidth / this.resolution);
       this.rows = Math.floor(p5.windowHeight / this.resolution);
       this.grid = [...Array(this.cols)].map(e => [...Array(this.rows)].map(value => Math.floor(2*Math.random())));
       canvas.position(0,0);
       canvas.style('z-index','-1')

   }

   draw = (p5) => {
       this.count += 1
       this.totcount += 1
     
       //if (this.count == 400) {
       //  this.count = 0
       //  this.setup()
       //}
      
       p5.background(248, 249, 252);
       for (let i = 0; i < this.cols; i++) {
         for (let j = 0; j < this.rows; j++) {
           let x = i * this.resolution
           let y = j * this.resolution
           if (this.grid[i][j] === 1) {
             p5.fill([197, 158, 187,50])
             p5.stroke([81, 4, 114, 80]);
             //p5.fill([123, 203, 183,50] )
             //p5.stroke([123, 203, 183,80] );
             p5.rect(x, y, this.resolution - 1, this.resolution - 1)
           }
         }
       }
         
       let next = this.grid.map(rows => rows.map(value => 0))

       for (let i = 0; i < this.cols; i++) {
         for (let j = 0; j < this.rows; j++) {
           let status = this.grid[i][j]
           let neighbors = this.countNeighbors(this.grid, i, j, this.cols, this.rows)
           if (status === 0 && neighbors === 3) {
             next[i][j] = 1
           } else if (status === 1 && (neighbors < 2 || neighbors > 3)) {
             next[i][j] = 0
           } else {
             next[i][j] = status
           }
     
         
       }
   }
       this.grid = next;
         
      
       
       if (this.count === 400) {
           p5.noLoop()
       }
       
   }


   countNeighbors = (grid, x, y, cols, rows) => {
       let sum = 0
       for (let i = -1; i < 2; i++) {
       for (let j = -1; j < 2; j++) {
           let xIndex = (x + i + cols) % cols
           let yIndex = (y + j + rows) % rows
           sum += grid[xIndex][yIndex]
       }
       }
       sum -= grid[x][y];
       return sum;
   }
    render(){
      
      
      const SlideList = [
        {
            textPosition: 'text-center',
            category: 'Front-End Developer',
            description: '',
            buttonText: '',
            buttonLink: ''
        }
    ]

    


    return(
                <div>
                <Helmet pageTitle="Taylor Epskamp" />
                {/* Start Header Area  */}
                <Header headerPosition="header--static" logo="symbol-dark" color="color-black"/>
                {/* End Header Area  */}
                
                

                <div className="slider-wrapper">
                <Sketch style = {{position:'relative'}} setup={this.setup} draw={this.draw} />
                    {/* Start Single Slide */}
                    {SlideList.map((value , index) => (
                        <div className="slide designer-portfolio slider-style-3  d-flex align-items-center justify-content-center  rn-slider-height" key={index}>
                            <div className="container">
                            
                                        <div className={`inner ${value.textPosition}`}>
                                            {value.category ? <span>{value.category}</span> : ''}
                                            <h1 className="title">Taylor Epskamp<br/></h1>
                                            <h2 style ={{color: '#510572'}}>based in Seattle.</h2>
                                            
                                        </div>
                                          
                            </div>
                        </div>
                    ))}
                    {/* End Single Slide */}
                </div>
                
                {/* Start Designer Portfolio  */}
                <div className="designer-portfolio-area ptb--120 bg_color--1">
                    <div className="wrapper plr--70 plr_sm--30 plr_md--30">
                        <TabThree column="col-lg-4 col-md-6 col-sm-6 col-12" />
                    </div>
                </div>
                {/* End Designer Portfolio  */}

                {/* Start Footer Style  */}
                <FooterTwo />
                {/* End Footer Style  */}
                
                {/* Start Back To Top */}
                <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div>
                {/* End Back To Top */}

            </div>
)}
}

export default App


